
import { defineComponent, ref } from 'vue'
import Remote from "@/remote";
import modal from "@/components/common/modal.vue";
import TAndC from "@/components/common/TAndC.vue";
import { GENERAL_ERROR } from '@/components/constant';
import moment from "moment";

enum availableErrorCodeMap {
  generic = "generic",
  configOff = "config-off",
  frequentLimit = "frequent-limit",
  noBank = "no-bank",
  salaried = "salaried",
  noContact = "no-contact",
  terminated = 'terminated',
  alreadyInAdvance = 'user-already-in-advance'
}

export default defineComponent({
  name: 'Intro',
  components: {TAndC, modal},
  props: {
    msg: String
  },

  data() {
    return {
      showNextPayDay: false,
      showModal: false,
      // isFeatureAvailable: true
      unAvailableReason: '',
      checkingFeature: true,
      nextPaymentDate: ''
    }
  },

  setup(props, context) {
    const showNextPayDay = true;
    const isFeatureAvailable = ref(false);
    return {showNextPayDay, isFeatureAvailable}
  },
  mounted() {
    this.$store.state.featureChecked = false;
    this.checkFeatureAvailable();
  },
  methods: {
    newRequest() {
      this.$router.push('/request')
    },
    checkFeatureAvailable() {
      this.checkingFeature = true;
      Remote.IsFeatureAvailable().then(res => {
        console.log('res', res)
        this.$store.state.featureChecked = true;
        this.$store.state.minimumRequestAmount = res.Response.MinimumRequestAmount;
        this.$store.state.nextPaymentDate = this.nextPaymentDate = moment(res.Response.NextPaymentDate).format('DD / MM / YYYY');
        this.$store.state.lastRequestDate = moment(res.Response.LastRequestDate).format('DD / MM / YYYY');
        this.$store.state.nextPayCycleDate = moment(res.Response.NextPayCycleDate).format('DD / MM / YYYY');

        if (res.Response.IsFeatureAvailable === false) {
          this.isFeatureAvailable = false
          this.unAvailableReason = this.getErrorMessageFromCode(res.Response.ErrorCode)
        } else {
          this.isFeatureAvailable = true
        }

      }, err => {
        this.isFeatureAvailable = false;
        this.unAvailableReason = GENERAL_ERROR
      })
      .finally(() => {
        this.checkingFeature = false;
      })
    },

    // currently not used until decided if client handle error message.
    getErrorMessageFromCode(errorCode: string): string {
      // unAvailableCode
      switch (errorCode) {
        case availableErrorCodeMap.generic:
          return `Sorry - Pay Advance is currently unavailable.`;
        case availableErrorCodeMap.configOff:
          return `Pay Advance is no longer available.`;
        case availableErrorCodeMap.frequentLimit:
          return `You can use Pay Advance once per pay cycle. Your last request was on ${this.$store.state.lastRequestDate}. Pay Advance will be available again on ${this.$store.state.nextPayCycleDate}`;
        case availableErrorCodeMap.noBank:
          return `We haven’t paid into that bank account yet. After a successful first payment is made, you will be able to use Pay Advance.`;
        case availableErrorCodeMap.salaried:
          return `Pay advance is currently unavailable because you're currently already paid in advance as a salaried employee. The next time you can use this feature after ${this.$store.state.nextPayCycleDate}`;
        case availableErrorCodeMap.terminated:
          return 'Pay Advance is currently unavailable. Please speak to your manager.';
        case availableErrorCodeMap.noContact:
          return `To use Pay Advance, your email address or mobile number need to be in the system. Speak to your manager or payroll adviser.`;
        case availableErrorCodeMap.alreadyInAdvance:
          return `Your salary is already paid in advance which means you can only use Pay Advance when there are unpaid funds. Try again between ${this.$store.state.nextPayCycleDate} and your next pay day.`
        default:
          return GENERAL_ERROR;
      }
    }
  }
})
